<template>
  <div class="company-info">
    <div class="name">
      <h6>基础信息</h6>
    </div>
    <div class="info">
      <p>
        <span>公司名称</span>
        <span>{{ companyInfo.companyName }}</span>
      </p>
      <p>
        <span>创建人</span>
        <span>{{ companyInfo.userName }}</span>
      </p>
      <p>
        <span>手机号</span>
        <span>{{ companyInfo.userMobile }}</span>
      </p>
      <p>
        <span>认证状态</span>
        <span>{{companyInfo.authenticationStatus}}</span>
      </p>
      <p>
        <span>公司地址</span>
        <span>{{ companyInfo.companyAddress }}</span>
      </p>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { shallowRef } from "vue";
import { useVueRouter } from "@assets/useHook";

import { agentDetailApi } from "@api/customer-management-api";

const { routeQuery } = useVueRouter();
const companyInfo = shallowRef({});
// 获取详情信息
const getDetail = async () => {
  try {
    const params = {
      agencyId: routeQuery.value.agencyId,
    };
    const { data } = await agentDetailApi.getData(params);
    companyInfo.value = agentDetailApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};

getDetail();
</script>
<style lang="scss" scoped>
.company-info {
  flex: 0 0 310px;
  height: 100%;
  background-color: #ffffff;

  .name {
    padding: 32px 32px 24px;
    border-bottom: 1px solid #edf1f6;

    h6 {
      font-size: 16px;
      font-weight: 900;
      color: #242934;
      line-height: 30px;
      //margin-bottom: 25px;
      word-break: break-all;
    }

    .icon {
      width: 14px;
      height: 14px;
      margin-right: 5px;
    }
    .certified {
      color: #9ca6b9;
      font-size: 12px;
      &.active {
        color: #2ed4a9;
      }
    }
  }

  .info {
    padding: 32px;

    p {
      display: flex;
      margin-bottom: 24px;

      span:nth-child(1) {
        flex: 0 0 80px;
        font-size: 14px;
        color: #9ca6b9;
        line-height: 20px;
      }

      span:nth-child(2) {
        flex-grow: 1;
        font-size: 14px;
        color: #646b7a;
        line-height: 20px;
      }
    }
  }
}
</style>
